import {connect} from "react-redux";
import React, {Component} from "react";
import OraModal from "eshop/modules/core/components/ui/Modal";
import {OraButton} from "eshop/components/OraCommonComponents";
import {getOutOfStockCheckoutErrors} from "../../selectors";
import {getCartCode} from "eshop/modules/cart/selectors";
import {dismissBackendValidationErrors, gotoCartSummary} from "../../actions/app";


class OutOfStockSapModalViewComponent extends Component {

    onClose(){
        this.props.dismiss();
    }

    renderInfo() {
        return (<span>Niektóre z zamówionych produktów nie są już dostępne.
            <br/> Możesz wrócić do koszyka i zmienić swój wybór, albo poczekać kilka dni, aż produkt będzie znowu dostępny.
        <br/> Koszyk zostanie zapisany i będzie można do niego wrócić.</span>);
    }

    render() {
        return (<OraModal showClose={true}   open={this.props.errors && this.props.errors.length > 0}  onClose={this.onClose.bind(this)} size="medium">
            <div className="u-padding-all-m u-small-padding-s">
                <div className="l-row u-letter-spacing-xs">
                    <div className="l-col l-col-12">
                        <h4>
                            {"Zamówienie nr "+ this.props.cartCode + " nie może być zrealizowane"}
                        </h4>
                    </div>
                </div>
                <div className="l-row u-spacing-top-m">
                    <div className="l-col l-col-12">
                        {this.renderInfo()}
                    </div>
                </div>
                <div className="l-row u-spacing-top-xl">
                    <div className="l-col l-col-12">
                        <h5>Nie możesz zamówić</h5>
                    </div>
                    <div className="l-col l-col-12">
                        <div className="l-row">
                            <div className="l-col l-col-3 l-col-small-12 l-col-medium-4">
                                <span>Zamawiane urządzenie</span>
                            </div>
                            <div className="l-col l-col-9 l-col-small-12 l-col-medium-8">
                                <span className="u-font-bold">{this.props.errors.map((error, i) => {
                                    return error.description;
                                })}</span>
                            </div>
                        </div>

                    </div>
                </div>
                <div className="l-row u-padding-top-m u-padding-m u-padding-left u-padding-right">
                    <hr/>
                </div>
                <div className="l-row u-spacing-top-m">
                    {!this.props.action ? null :
                        <div className="l-col l-col-small-12 l-col-medium-4 l-col-4 medium-offset-by-8 large-offset-by-8 u-text-right">
                            <OraButton onClick={this.props.action} className="u-w-100 ">Wróć do koszyka</OraButton>
                        </div>
                    }
                </div>
            </div>
        </OraModal>);
    }
}


let mapStateToProps = state => ({
    errors: getOutOfStockCheckoutErrors(state),
    cartCode: getCartCode(state)
});
let mapDispatchToProps = dispatch => ({
    dismiss: () => dispatch(dismissBackendValidationErrors()),
    action: () => dispatch(gotoCartSummary())
});

const OutOfStockSapModal = connect(mapStateToProps, mapDispatchToProps)(OutOfStockSapModalViewComponent);
export default OutOfStockSapModal;
